} */

.btn {
  position: absolute;
  display: inline-block;
  padding: 1px;

  width: 100px;
  height: 50px;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  cursor: pointer;
  border: #808080 solid 1px;
  border-radius: 4px;
  background-color: #fff;
}

.btn:hover, .btn:focus {
  background-color: #ede6e6;
}

.btn-start {
  top: 50%;
  left: 50%;
  transform: translate(-150%, -50%);
}

.btn-stop {
  top: 50%;
  left: 50%;
  transform: translate(-40%, -50%);
}
